



























































import Vue from "vue";

export default Vue.extend({
    components: {},
    data() {
        return {
            d_login: true,
            username: null,
            password: null
        }
    },
    methods: {
        login_res() {
            var self = this;
            this.$http.post('/login', {"username": this.username, "password": this.password}).then(({data}) => {
                if (data.status === 200) {
                    var token = data.data;
                    localStorage.setItem("token", this.username + "_" + token);
                    this.$router.push("/").catch(_ => {
                    });
                    location.reload();


                } else {
                    alert(data.msg);
                }
            })
        }
    }
});
